<template>
  <div class="appointment">
    <mt-header>
      <a @click="goBack" slot="left">
        <mt-button icon="back">预约确认</mt-button>
      </a>
    </mt-header>
    <div class="appointmentBody">
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">被护理人</span>
          </div>
          <div class="w_cell_value">
            <span>{{beCaredDetail.name}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务类型</span>
          </div>
          <div class="w_cell_value">
            <span>{{glodalVal.jrOrJx}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">陪护分类</span>
          </div>
          <div class="w_cell_value">
            <span>{{glodalVal.companyType}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell" v-if="glodalVal.jrOrJx == '计日类'">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务时间</span>
          </div>
          <div class="w_cell_value">
            <span @click="showTime = true">{{time}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell" v-else>
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务时间</span>
          </div>
          <div class="w_cell_value">
            <ul>
              <li v-for="t in JXtime">{{t}}</li>
            </ul>
            <span @click="showTime = true">{{time}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell" v-if="type == 'ys'">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">选择门店</span>
          </div>
          <div class="w_cell_value">
            <span @click="storeChange">{{store}}</span> 
          </div>
        </div>
      </a>
      <!-- <p v-if="glodalVal.jrOrJx == '计日类'"><span>服务时间</span><mt-button @click.native=" showTime = true" size="small" ref="button">{{time}}</mt-button></p> -->
      <!-- <p v-else><span>服务时间</span><mt-button @click.native=" showTime = true" size="small" ref="button">{{time}}</mt-button></p> -->
      <!-- <div v-else>
        <span>服务时间</span>
        <div style="display:inline-block">
          <ul>
            <li v-for="t in JXtime">{{t}}</li>
          </ul>
          <mt-button @click.native=" showTime = true" size="small" ref="button">点击选择时间</mt-button>
        </div>
      </div>  -->
      <a class="w_cell" v-if="glodalVal.jrOrJx == '计项类'">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务时长</span>
          </div>
          <div class="w_cell_value">
            <span>{{duration}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell" v-if="type == 'ys'">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务地址</span>
          </div>
          <div class="w_cell_value">
            <span><input class="w_core" type="text" placeholder="填写家庭地址" v-model="address"></span>
          </div>
        </div>
      </a>
      <div v-else>
        <a class="w_cell">
          <div class="w_cell_wraper">
            <div class="w_cell_title">
              <span class="w_cell_text">服务地址</span>
            </div>
            <div class="w_cell_value">
              <span @click=" showHospital = true">{{hospital}}</span> 
            </div>
          </div>
        </a>
        <a class="w_cell">
          <div class="w_cell_wraper">
            <div class="w_cell_title">
              <span class="w_cell_text"></span>
            </div>
            <div class="w_cell_value">
              <span @click="departmentsChange">{{departments}}</span> 
              <span><input class="w_core" type="text" placeholder="填写病区床号" v-model="region"></span>
            </div>
          </div>
        </a>
      </div>
      <a class="w_cell" style="min-height:1rem;">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">补充说明</span>
          </div>
          <div class="w_cell_value" style="border:none;"></div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <textarea v-model="remark" class="w_field_core" placeholder=""  rows="5"></textarea>
        </div>
      </a>
      <mt-popup v-if="glodalVal.jrOrJx == '计日类'" v-model="showTime" popup-transition="popup-fade">
        <mt-picker :slots="timeSlots" @change="onTimeChange" :visible-item-count="5"></mt-picker>
        <mt-button @click.native="handleInnerFirstChange" size="small">确定</mt-button>
     </mt-popup>
     <mt-popup v-else v-model="showTime" popup-transition="popup-fade">
        <mt-button @click.native="handleJXInnerFirstChange" size="small">确定</mt-button>
        <mt-picker :slots="timeJXSlots" @change="onTimeChange" :visible-item-count="5"></mt-picker>
     </mt-popup>
     <mt-popup v-model="showDay" popup-transition="popup-fade">
        <mt-button @click.native="handleInnerSecondChange" size="small">确定</mt-button>
        <mt-picker :slots="daySlots" @change="onDayChange" :visible-item-count="5"></mt-picker>
     </mt-popup>
     <mt-popup v-model="showHospital" popup-transition="popup-fade">
        <mt-button @click.native="handleShowHospitalChange" size="small">确定</mt-button>
        <mt-picker :slots="hospitalSlots" @change="onHospitalChange" :visible-item-count="5"></mt-picker>
     </mt-popup>
     <mt-popup v-model="showDepartments" popup-transition="popup-fade">
        <mt-button @click.native="handleShowDepartmentsChange" size="small">确定</mt-button>
        <mt-picker :slots="departmentsSlots" @change="onDepartmentsChange" :visible-item-count="5"></mt-picker>
     </mt-popup>
     <mt-popup v-model="showStore" popup-transition="popup-fade">
        <mt-button @click.native="handleShowStore" size="small">确定</mt-button>
        <mt-picker :slots="storeSlots" @change="onStoreChange" :visible-item-count="5"></mt-picker>
     </mt-popup>
    </div>
    <div class="footer">
      <!-- <div class="price">
        ¥ 200
      </div> -->
      <p class="price">
        ¥ {{price}}
      </p>
      <mt-button @click.native="handleAppoitment" size="small">立即预约</mt-button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import {oneMonthTime, timeArr, dayArr,timeJXArr} from '../utils/oneMonthTime'
import { Toast } from 'mint-ui';
export default {
  name: 'appointment',
  data () {
    return {
      id: '',
      type: '',
      time: '点击选择服务时间',
      hospital: '选择所在医院',
      departments: '所在科室',
      store: '点击选择门店',
      hospitalId: null,
      showTime: false,

      showDay: false,
      showHospital: false,
      showDepartments: false,
      showStore: false,
      region: '',
      timeSlots: [
          {
            flex: 1,
            values: Object.keys(oneMonthTime),
            className: 'slot1',
            textAlign: 'center'
          }, {
            divider: true,
            content: ' ',
            className: 'slot2'
          }, {
            flex: 1,
            values: timeArr,
            className: 'slot3',
            textAlign: 'center'
          }
        ],
        timeJXSlots: [
         {
            flex: 1,
            values: Object.keys(oneMonthTime),
            className: 'slot1',
            textAlign: 'center'
          }, {
            divider: true,
            content: ' ',
            className: 'slot2'
          }, {
            flex: 1,
            values: timeJXArr,
            className: 'slot3',
            textAlign: 'center'
          }
        ],
        timeDay: Object.keys(oneMonthTime)[0],
        timeDate: '00:00',
        daySlots: [
        {
          flex: 1,
          values: dayArr,
          className: 'slot1',
          textAlign: 'center'
        }],
        day: 1,
        hospitalSlots: [
          {
            flex: 1,
            values: '',
            className: 'slot1',
            textAlign: 'center'
          }
        ],
        departmentsSlots: [
          {
            flex: 1,
            values: '',
            className: 'slot1',
            textAlign: 'center'
          }
        ],
        storeSlots: [
          {
            flex: 1,
            values: '',
            className: 'slot1',
            textAlign: 'center'
          }
        ],
        JXtime:[],
        duration: '',
    }
  },
  watch: {
    day: function(val, oldVal) {
      if(val != oldVal) {
        this.price = this.day * this.glodalVal.companyType.split(' ')[1];
      };
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
        vm.type = to.query.type;
        console.log(to.query.type);
        vm.duration = to.query.duration;
        vm.getBecaredDetail(vm.glodalVal.beCaredId).catch(error => {
          if(error.body.code == 3001) {
            this.$router.push('/login');
          }
        });
        vm.id = to.query.id;
        let hospital = [];
        if(vm.hospitals.items.length >= 0){
          vm.hospitals.items.forEach(function(i) {
            hospital.push(i.name);
          })
        }
        vm.hospitalSlots[0].values = hospital;
        if(to.query.type == 'ys') {
          vm.getZoneStore(vm.glodalVal.currentCity)
        }
    })
  },
  computed: {
    ...mapGetters({
      beCaredDetail: 'getBeCaredDetail',
      glodalVal: 'getGlodalVal',
      hospitals: 'getHospitals',
      allDepartments: 'getDepartments',
      citiesProvince: 'getCities',
      myServices: 'getMyServices'
    }),
    price: function() {
      if(this.type == 'ys') {
        return this.glodalVal.companyType.match(/\d+/)[0];
      } else {
        return this.day * parseInt(this.glodalVal.companyType.split(' ')[1]);
      }
    }
  },
  methods: {
    ...mapActions({
      getBecaredDetail: 'getBecaredDetail',
      fetchDepartments: 'fetchDepartments',
      addAppointment: 'addAppointment',
      addMyAppointment: 'addMyAppointment',
      addJXAppoinment: 'addJXAppoinment',
      getZoneStore: 'getZoneStore',
      addYSAppoinment: 'addYSAppoinment',
      changeTabSelected: 'changeTabSelected'
    }),
    goBack: function() {
      history.go(-1);
    },
    departmentsChange: function() {
      let departmentsArr = [];
      this.allDepartments.forEach(function(i) {
        departmentsArr.push(i.name);
      })
      this.departmentsSlots[0].values = departmentsArr;
      this.showDepartments = true;
    },
    storeChange: function() {
      console.log(1);
      let storeArr = [];
      this.glodalVal.store.forEach(i => {
        storeArr.push(i.name);
      })
      this.storeSlots[0].values = storeArr;
      this.showStore = true;
    },
    onHospitalChange: function(picker, values) {
      this.hospital = values[0];
    },
    onTimeChange: function(picker, values) {
        picker.setSlotValues(1, oneMonthTime[values[0]]);
        this.timeDay = values[0];
        this.timeDate = values[1];
      },
      handleInnerFirstChange: function() {
        const year = this.timeDay.slice(0, 4);
        const month = this.timeDay.slice(5, 7);
        const day = this.timeDay.slice(8, 10);
        const hour = this.timeDate.slice(0, 2);
        const minute = this.timeDate.slice(3, 5);
        const time = year + '-' + month + '-' + day + ' '+ hour + ':' + minute;
        if(Date.now() > new Date(time).getTime() ) {
          Toast({
              message: '预约时间小于当前时间，请重新选择',
              position: 'bottom',
              duration: 1000
            });
            this.showTime = true;
            this.showDay = false;
            return;
        } else {
            if(this.type == 'ys') {
              this.showTime = false;
              this.time = this.timeDay + '  ' + this.timeDate;
            } else {
              this.showTime = false;
              this.showDay = true;
            }
        }
      },
      handleShowStore: function() {
        this.showStore = false;
      },
      handleJXInnerFirstChange: function() {
        const year = this.timeDay.slice(0, 4);
        const month = this.timeDay.slice(5, 7);
        const day = this.timeDay.slice(8, 10);
        const hour = this.timeDate.slice(0, 2);
        const minute = this.timeDate.slice(3, 5);
        const time = year + '-' + month + '-' + day + ' '+ hour + ':' + minute;
        if(Date.now() > new Date(time).getTime() ) {
          Toast({
              message: '预约时间小于当前时间，请重新选择',
              position: 'bottom',
              duration: 1000
            });
            this.showTime = true;
            this.showDay = false;
            return;
        } else {
            const timeStr = this.timeDay + ' ' + this.timeDate;
            this.JXtime.push(timeStr);
            this.showTime = false;
        }

      },
      handleInnerSecondChange: function() {
        this.showTime = false;
        this.showDay = false;
        this.time = this.timeDay + '' + this.timeDate + ' ' + this.day + '天';
      },
      onDayChange: function(picker, values) {
         this.day = values[0];
      },
      handleShowHospitalChange: function() {
        const items = this.hospitals.items;
        let id;
        for(let i = 0; i < items.length; i++) {
          if(items[i].name == this.hospital) {
            id = items[i].id;
          }
        }
        this.hospitalId = id;
        this.fetchDepartments(id).catch(error => {
          if(error.body.code == 3001) {
            this.$router.push('/login');
          }
        });
        this.showHospital = false;
      },
      handleShowDepartmentsChange: function() {
        this.showDepartments = false;
      },
      onStoreChange: function(picker, values) {
        this.store = values[0];
      },
      onDepartmentsChange: function(picker, values) {
        this.departments = values[0];
      },
      handleAppoitment: function() {
          const departments = this.allDepartments;
          let departmentId;
          for(let i = 0; i < departments.length; i++) {
            if(departments[i].name == this.departments) {
              departmentId = departments[i].id;
            }
          }
          let province = '';
          const cityProvince = this.citiesProvince;
          cityProvince.forEach(i => {
            if(i.city == this.glodalVal.currentCity) {
              province = i.province;
            }
          })
          let obj = {};
          obj.beCareId = this.glodalVal.beCaredId;
          obj.hospitalId = this.hospitalId;
          obj.departmentId = departmentId;
          obj.region = this.region;
          obj.remark = this.remark;
          obj.city = this.glodalVal.currentCity;
          obj.province = province;
          obj.serviceDate = this.timeDay.replace(/\./g,'-') + ' ' + this.timeDate;
          obj.serviceDurationUnit = 3;
          obj.serviceDuration = this.day;
        if(this.type == 'my') {
          obj.infantMom = this.glodalVal.infantMom;
          obj.infantMom.serviceId = this.id;
          this.addMyAppointment(obj).then(res => {
            if(res.ok) {
              this.changeTabSelected('订单')
              this.$router.push({path:'/index', query : { 'back': 'order' }});
            }
          }, error => {
            if(error.body.code == 3001) {
              this.$router.push('/login');
            }
          })
        } else if(this.type == 'jr') {
          console.log(2);
          obj.diseaseCare = {
            "serviceId": this.id,
            "selfCareAbility": this.glodalVal.selfCareAbility,
            "illnessLevel": this.glodalVal.illnessLevel
          }
          this.addAppointment(obj).then(res => {
            if(res.ok) {
              this.changeTabSelected('订单')
              this.$router.push({path:'/index', query : { 'back': 'order' }});
            }
          },error => {
            if(error.body.code == 3001) {
              this.$router.push('/login');
            }
          });
        } else if(this.type == 'jx') {
          let obj = {};
          obj.beCareId = this.glodalVal.beCaredId;
          obj.hospitalId = this.hospitalId;
          obj.departmentId = departmentId;
          obj.region = this.region;
          obj.remark = this.remark;
          obj.city = this.glodalVal.currentCity;
          obj.province = province;
          obj.serviceDates = this.JXtime;
          obj.serviceDuration = this.duration;
          obj.serviceDurationUnit = 5;
          obj.itemClass = {
            serviceIndex: this.glodalVal.serviceIndex,
            serviceId: this.id
          }
          this.addJXAppoinment(obj).then(res => {
            if(res.ok) {
              this.changeTabSelected('订单')
              this.$router.push({path:'/index', query : { 'back': 'order' }});
            }
          }, error => {
            if(error.body.code == 3001) {
              this.$router.push('/login');
            }
          });
        } else if(this.type == 'ys') {
          obj.address = this.address;
          obj.serviceDuration = this.glodalVal.companyType.match(/\/(\d+)/)[1];
          obj.infantMom = this.glodalVal.infantMom;
          obj.infantMom.serviceId = this.id;
          this.glodalVal.store.forEach(i => {
            if(i.name == this.store) {
               obj.storeId = i.id;
            }
          })
          this.addYSAppoinment(obj).then(res => {
            if(res.ok) {
              this.changeTabSelected('订单')
              this.$router.push({path:'/index', query : { 'back': 'order' }});
            }
          }, error => {
            if(error.body.code == 3001) {
              this.$router.push('/login');
            }
          });
        }
      }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.appointment {
  font-size: 0.4rem;
}
.appointment .mint-cell-value {
  flex: 4;
}
.appointment .mint-popup {
  width: 65%;
}
.appointment .footer{
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
    width: 100%;
    background-color: #fff;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #d06091;
}
.appointment .footer .price {
  height: 1rem;
  margin-left: 0.5rem;
  line-height: 1rem;
  color: #ff8f0b;
}
.appointment .footer .mint-button {
  background-color: #d06091;
  color: #fff;
}

/*w_popup*/

</style>



